import React, { useState } from "react"
import { useHistory } from "react-router-dom"
import { Form, Button } from "react-bootstrap"

import styles from "./Login.module.scss"

const Login = ({ updateLoginDetailsHandler }) => {
    const [guestName, setGuestName] = useState(null);
    const [invitation, setInvitation] = useState(null);
    const routerHistory = useHistory();

    const onFetchInvitationResponse = (invitation) => {
        setInvitation(invitation);
        updateLoginDetailsHandler({ guestName, invitation });
        routerHistory.goBack();
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        fetchInvitation(guestName, (e) => onFetchInvitationResponse(e));
    }

    return (
        <>
            <Form className={styles.sign_in + " pull-left"} onSubmit={(e) => handleSubmit(e)}>
                <Form.Group>
                    <Form.Label>Name</Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="Colin Marshall"
                        onChange={(e) => setGuestName(e.target.value)}
                    ></Form.Control>
                    <Form.Text>Please enter your first and last name as it appears on your invitation</Form.Text>
                </Form.Group>
                <Button
                    variant="light"
                    type="submit"
                >
                    Submit
                </Button>
            </Form>
            <h1>Hello there: {guestName}</h1>
            <h2>Invitation: {JSON.stringify(invitation)}</h2>
        </>
    )
}

function fetchInvitation(name, callback) {
    var requestOptions = {
        method: 'GET',
    };

    fetch("https://d0wdlrbbf3.execute-api.us-west-2.amazonaws.com/invitation/" + name, requestOptions)
        .then(response => response.json())
        .then(result => callback(result))
        .catch(error => console.log('error', error));
}

export default Login
