import React, { useState } from "react"
import { Route, Switch } from "react-router-dom"


import { Home } from "views/Home"
import { Schedule } from "views/Schedule"
import { Travel } from "views/Travel"
import { ThingsToDo } from "views/ThingsToDo"
import { FAQ } from "views/FAQ"
import { Registry } from "views/Registry"
import { RSVP } from "views/RSVP"
import { Login } from "components/Login"

const AppLayout = () => {

    const [loginDetails, setLoginDetails] = useState(null);

    const updateLoginDetailsHandler = (updatedLoginDetails) => {
        console.log(updatedLoginDetails);
        setLoginDetails(updatedLoginDetails);
    }

    const createRoute = (path, Component) => {
        return (
            <Route
                exact path={path}
                render={(props) => (
                    <Component {...props} updateLoginDetailsHandler={updateLoginDetailsHandler} />
                )}
            />
        )
    }

    return (
        <>
            <main className="mb-4">
                <Switch>
                    {createRoute("/", Home)};
                    {createRoute("/schedule", Schedule)};
                    {createRoute("/travel", Travel)};
                    {createRoute("/things-to-do", ThingsToDo)};
                    {createRoute("/faq", FAQ)};
                    {createRoute("/registry", Registry)};
                    {createRoute("/rsvp", RSVP)};
                    {createRoute("/login", Login)};
                </Switch>
            </main>
        </>
    );

}

export default AppLayout
